import React from "react";
import style from "./LinkAccountArea.less";

import firebase from "lib/firebase";
import firebase2 from "firebase";
import BigButton from "../BigButton/BigButton";

export default class LinkAccountArea extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      status: "start"
    };
  }

  signInWithGoogle() {
    console.log("sign in");
    var provider = new firebase2.auth.GoogleAuthProvider();
    this.setState({ status: "loading" }, () => {
      firebase
        .auth()
        .currentUser.linkWithPopup(provider)
        .then(result => {
          // This gives you a Google Access Token. You can use it to access the Google API.
          var token = result.credential.accessToken;
          // The signed-in user info.
          var user = result.user;
          console.log(result);
          this.setState({ status: "done" });
          // ...
        })
        .catch(error => {
          console.error(error.code);
          if (error.code == "auth/credential-already-in-use") {
            this.setState({ status: "already" });
          } else {
            this.setState({ status: "error" });
          }
          // Handle Errors here.
          var errorCode = error.code;
          var errorMessage = error.message;
          // The email of the user's account used.
          var email = error.email;
          // The firebase.auth.AuthCredential type that was used.
          var credential = error.credential;
          // ...
        });
    });
  }
  render() {
    return (
      <div styleName="LinkAccountArea">
        {this.state.status != "already" ? (
          <BigButton
            loading={this.state.status == "loading"}
            onClick={() => {
              this.signInWithGoogle();
            }}
          >
            {this.state.status == "start" && "Sign in with google"}
            {this.state.status == "error" && "Something went wrong."}
            {this.state.status == "done" && "Completed!"}
          </BigButton>
        ) : (
          <div styleName="alreadyLinked">
            Account already in use. Click here to log out, then log back in with
            the right account.
          </div>
        )}
      </div>
    );
  }
}
